.list {
  list-style: none;
  font-size: 16px;
  line-height: 18px;
  padding-left: 24px;
  padding-bottom: 10px;
}
.listItem:not(:last-child) {
  margin-bottom: 10px;
}
.link, .link:hover {
  text-decoration: none;
  color: #FFFFFF;
  cursor: pointer;
  font-weight: 300;
}
@media(min-width: 1200px) {
  .list {
    padding: 0;
  }
}
.listItem:not(:last-child) {
  margin: 0;
}
.link {
  padding: 5px 20px;
  display: block;
  transition: all .3s ease;
}
.link:hover {
  background: rgba(96, 182, 217, .7);
}
